body {
  margin: 0;
  background: white;
}

.mainText {
  position: relative;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 80%;
  left: 20%;
  background-color: white;
  transition: 0.3s;
  z-index: 1;
}

div.navigationBar {
  float: left;
  background-color: black;
  left: 0px;
  height: 100%;
  width: 20%;
  margin: 0;
  padding: 0;
  z-index: 2;
}

ul.navigationBar {
  text-align: left;
  list-style-type: none;
  background-color: black;
  height: 100%;
  width: 20%;
  margin: 0;
  padding: 0;
  position: fixed;
  overflow-x: hidden;
  overflow-y: auto;
  transition: width 0.2s;
  z-index: 2;
}

li a.navHeader {
  border-bottom-style: groove;
  border-width: 2px;
  display: block;
  text-decoration: none;
  margin: 4px;
  padding: 2px;
  color: #818181;
  transition: 0.3s;
}

li a.navLink {
  border-bottom-style: dotted;
  border-width: 1px;
  display: block;
  text-decoration: none;
  margin-left: 14px;
  padding: 1px;
  width: 100%;
  color: #818181;
  transition: 0.3s;
}

li a.navLink:hover {
  color: #f1f1f1;
}

li a.navHeader:hover {
  color: #f1f1f1;
}

.active, .collapsible:hover {
  background-color: #ccc;
}

.collapsibleContent {
  float: left;
  max-width: 0;
  padding: 0px;
  overflow: hidden;
  background-color: #f1f1f1;
}

.opentbn {
  display: flex;
  font-size: 20px;
  text-decoration: none;
  color: black;
}

.closetbn {
  font-size: 40px;
  text-decoration: none;
  color: #818181;
  transition: 0.3s;
}

.closetbn:hover {
  color: #f1f1f1;
}
