.scrollarea {
  z-index: 1;
  position: fixed;
  width: 100%;
  height: 105px;
  top: 0px;
  left: 0px;
}

div.events {
  transition: all 0.2s;
  position: absolute;
  opacity: 0.4;
  width: 75px;
  height: 75px;
  left: 15px;
  top: 15px;
}

div.events:hover {
  width: 80px;
  height: 80px;
  left: 12.5px;
  top: 12.5px;
  opacity: 0.8;
}

img.events {
  opacity: inherit;
  width: 100%;
  height: 100%;
  z-index: 1;
}

