/*
 * Main color pallete:
 * Yellow: #FCC141; 252 193  65;
 * Red:    #E94D34; 233  77  52;
 * Pink:   #EB527E; 235  82 126;
 * lPink:  #F4ABC8; 244 171 200;
 * lBlue:  #8FD2E5; 143 210 229;
 */

.splash-header {
 font-family: 'Ubuntu';
 font-size: 40px;
 display: block;
 margin: 0;
}

.typed {
  font-family: 'Ubuntu';
  font-size: 40px;
  display: block;
}

.vision {
  font-family: 'Quicksand';
  font-size: 20px;
  text-align: left;
  margin-bottom: 0px;
}

.quote {
  text-align: left;
  padding-bottom: 10px;
}

.caption {
  text-align: left;
}

.quote-card {
  min-width: 200px;
  margin-top: 20px;
  width: 100%;
}

.scroll-flex {
  height: 100%;
  display: flex;
}

.timeline-card {
  width: 30%;
  height: 100%;
  min-width: 275px;
  max-width: 400px;
  margin-right: 100px;
}

.timeline-action-area {
  width: 100%;
  height: 100%;
}

.timeline-card-media {
  width: 100%;
  height: 65%;
}

.timeline-card-text {
  height: 35%;
}

.timeline-scroll-area {
  position: relative;
  margin-top: 20px;
  width: 100%;
  height: 27vh;
}

.Splash {
  position: relative;
  overflow: hidden;
  text-align: center;
  background-color: #141414;
}

.Splash-top:before {
  content: ' ';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.Splash-top {
  position: relative;
  height: 100%;
}

.Splash-bottom {
  position: relative;
  background-color: #FA8072;
  margin: 20px;
  padding: 20px;
  height: 100%;
}

.About-us {
  position: relative;
  background-color: #8FD2E5;
  padding: 30px;
  overflow: auto;
}

.About-us-content {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.Timeline {
  position: relative;
  height: auto;
  background-color: #8FD2E5;
  margin-top: 20px;
  padding: 30px;
}

.Splash-logo {
  overflow: visible;
  width: 40%;
  max-width: 512px;
  min-width: 250px;
  height: auto;
  display: inline-block;
  vertical-align: middle;
}

.flyer {
  z-index: 1;
  transition: all 0.2s;
  position: absolute;
  overflow: visible;
  width: 30%;
  height: auto;
  left: 20px;
  bottom: 20px;
}

.flyer:hover {
  width: 55%;
}

.typeform {
  z-index: 2;
  position: fixed;
  height: 90% !important;
  width: 90% !important;
  left: 5%;
  top: 5%;
}

#streak {
  position: absolute;
  overflow: hidden;
}
